@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.5.2/css/all.min.css";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.menu {
  background: #acacac;
  width: 25%;
  height: 100vh;
}

.menu .menu-btns {
  flex-direction: column;
  display: flex;
}

.menu .menu-btns .menu-btn {
  background-color: #918f8f;
  border: none;
  border-radius: 20px;
  width: 150px;
  height: 30px;
  margin: 0 auto 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.menu .menu-btns .menu-btn:last-child {
  margin-bottom: 50px;
}

.menu .request-history {
  flex-direction: column;
  width: 85%;
  margin: auto;
  display: flex;
}

.menu .request-history .request {
  color: #202020;
  margin: 0 auto 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  font-weight: 300;
}

.greeting {
  text-align: center;
  width: 650px;
  height: 200px;
  margin: 50px auto 20px;
}

.messages {
  background: #dedddd;
  flex-direction: column;
  justify-content: space-between;
  width: 75%;
  height: 100vh;
  display: flex;
}

.messages .response {
  flex-direction: column-reverse;
  width: 90%;
  max-height: 300px;
  margin: 0 auto 30px;
  padding: 0 60px 30px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  font-weight: 400;
  display: flex;
  overflow-y: scroll;
}

.messages .form-request {
  background-color: #fff;
  border: none;
  border-radius: 30px;
  width: 750px;
  height: 50px;
  margin: 0 auto 70px;
  display: flex;
}

.messages .form-request .request-input {
  border: none;
  border-radius: 30px;
  width: 700px;
  margin-right: 5px;
  padding: 25px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 18px;
}

.messages .form-request .request-button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  overflow: hidden;
}

.messages .form-request .request-button .fa-paper-plane {
  font-size: 25px;
}

.container {
  z-index: 3;
  max-width: 1170px;
  margin: auto;
  padding: 0 15px;
  position: relative;
}

h1 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 52px;
  font-weight: 700;
}

h2 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 42px;
  font-weight: 700;
}

h3 {
  text-transform: uppercase;
  margin: 10px auto 15px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 36px;
  font-weight: 700;
}

.ai-tools {
  width: 100vw;
  height: 100vh;
  display: flex;
}
